.tapBar{
    background-color: #1D1061;
    position: fixed;
    bottom: 0%;
    width: 100%;
    text-align: center;
    height: 8%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    font-size: 1.5rem;
    align-items: center;
    box-shadow: 0px 2px 30px white;
}

.tapBarLight{
    background-color: rgb(120, 120, 255);
    position: fixed;
    bottom: 0%;
    width: 100%;
    text-align: center;
    height: 8%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    font-size: 1.5rem;
    align-items: center;
    border-radius: 22px;
    background: linear-gradient(225deg, rgb(120, 120, 255), #7056f1);
    box-shadow:  -10px 10px 20px #c5c5c5,
                 10px -10px 20px #fbfbfb;
}
.darktheme {
    background-color: rgb(70, 68, 68);
    color: #fff;
}

.lightTheme{
    background-color: #eaebf8;
    color: rgb(45, 46, 44);
}
.button{
    color: white;
}

.buttonLight{
    color: #21266B;
}

.Light{
    color: rgb(255, 153, 0);
}
.Dark{
    color: rgb(200, 184, 68);
}

.buttonMenuLight{
    border: 1px solid rgb(120, 120, 255);
    color: rgb(120, 120, 255);
    padding: 1%;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    box-shadow: 2px 2px 2px ;
    margin-bottom: 10%;
    font-size: 1.5rem;
}

.buttonMenu{
    border: 1px solid #21266B;
    color: #21266B;
    padding: 1%;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    box-shadow: 2px 2px 2px ;
    margin-bottom: 10%;
    font-size: 1.5rem;
}